* {margin:0;padding:0;}
html {

}


.item{
    width: 130px;
}
.icon{
    width:56px;
    height: 56px;
    transform:translateZ(0)

}
.icon-mongo{
    width:130px;
    height: 56px;
    transform:translateZ(0)
}
.icon-mongoose{
    width:112px;
    height: 56px;
    transform:translateZ(0)
}
.icon-node {
    width:70px;
    height: 56px;
    transform:translateZ(0)
}
.container1 {
    width:100%;
    height:100%;


}
.navbar1 {
    padding: 0;
    position:fixed;
    top:0;
    z-index:1;
    display:flex;
    width:100%;
    height:10%;
    background:rgba(0,0,0,0.4);
}
.navbar1 ul {
    padding: 0;
    list-style:none;
    width:100%;
    justify-content:center;
    align-items:center;
}
.navbar1 ul li {
    padding: 0;
    margin: 0 1rem;

    align-items:center;
    width: auto;
}

.navbar1 ul li a {
    text-decoration:none;
    text-transform:uppercase;
    color:#f4f4f4;
}

.navbar1 ul li a:hover {
    color:skyblue;
}
.li-item{
     text-align: left;
     font-size: 20px;
     list-style-type: decimal;
 }
.item:hover{
    color: darkorange;
}
section {
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    text-align:center;
    width:100%;
    height:100vh;
    scroll-snap-align:center;
}
.works-li{
    width: 120px;
}
.works-li:hover{
    color: darkorange;
}
section h1 {
    font-size:3rem;
}
section h2 {
    font-size:2.5rem;
}
section a {
    color: white;
    font-size:1.5rem;
}
section p {
    color: white;
    font-size:1.5rem;
}

/* Section Images */
section#home {
    background: url('../img/home.jpg') no-repeat center center/cover;
}
section#about {
    background: url('../img/stack.webp') no-repeat center center/cover;
}

section#stack {
    background:url('http://school298.spb.ru/images/600/DSC100622595.jpg') no-repeat center center/cover;
}

section#works {
    background:url('../img/tex0227.jpg') no-repeat center center/cover;
}

section#contacts {
    background:url('../img/Offensive-email-warrants-sacking.jpg') no-repeat center center/cover;
}


/*Smartphones*/
@media (max-width: 430px){

    .text-ul{
        padding-left: 2rem;
        padding-right: 15px;
    }
    .stack{
        padding-top: 50px;
    }
    .item{
        width: 100px;
    }
    .icon{

        width:28px;
        height: 28px;
        transform:translateZ(0)
    }
    .icon-mongo{
        width:80px;
        height: 28px;
        transform:translateZ(0)
    }
    .icon-mongoose{
        width:56px;
        height: 28px;
        transform:translateZ(0)
    }
    .icon-node {
        width:35px;
        height: 28px;
        transform:translateZ(0)
    }

    .navbar1 {
        display:inline;
        height:auto;
        font-size: 0.8rem;
        
    }

    .navbar1 ul li {
        margin: 0 1rem;
        padding:0.7rem;
    }

    .navbar1 ul li a {
        line-height: 1;
        padding: 0;
    }


    .li-item{
        font-size: 14px;
    }
    
    section h1 {
        font-size:1.5rem;
    }
    section h2 {

        font-size:1rem;
    }
    section a {

        font-size:1rem;
    }
    section p {
        font-size:1rem;
    }

    /* Section Images */
    section#home {
        height: 100vh;
    }
    section#about {
        height: 130vh;
    }


    section#stack {

    }

    section#works {
    }

    section#contacts {
    }
}
